import { SET_TASK, RESET_TASK } from "../actions/current-task-actions";

export default function currentTaskReducer(state = {
    userId: 0,
    id: 0,
    title: "",
    completed: false }, {type, payload}) {  
  if (type == SET_TASK) {
    return(payload);
  } else if (type == RESET_TASK) {
    return(payload);
  } else {
    return(state);
  }
} 