export const SET_TASK = 'currentTask:setTask';
export const RESET_TASK = 'currentTask:resetTask';

export function setTask(task) {
    return ({
        type: SET_TASK,
        payload: {
            userId: Number(task.userId),
            id: Number(task.id),
            title: task.title,
            completed: Boolean(task.completed)
        }
    });
}

export function resetTask() {
    return ({
        type: RESET_TASK,
        payload: {
            userId: 0,
            id: 0,
            title: "",
            completed: false
        }
    });
}