import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withCookies } from 'react-cookie';
import { setTask, resetTask } from "../actions/current-task-actions";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.clickHandler = this.clickHandler.bind(this);
    this.resetHandler = this.resetHandler.bind(this);
    this.removeCookies = this.removeCookies.bind(this);
    this.addCookies = this.addCookies.bind(this);
    this.testAndroidInterface = this.testAndroidInterface.bind(this);
  }

  clickHandler() {
    fetch('https://jsonplaceholder.typicode.com/todos/1').then(response => response.json()).then((e) => {
      this.props.onSetTask(e);
    });
  }

  resetHandler() {
    this.props.onResetTask();
  }

  removeCookies() {
    const { cookies } = this.props;
    cookies.remove("authToken", {path:"/"});
    cookies.remove("authUser", {path:"/"});
  }

  addCookies() {
    const { cookies } = this.props;
    cookies.set("authToken", "Val1", {path:"/"});
    cookies.set("authUser", "Val2", {path:"/"});
  }

  testAndroidInterface(toast) {
    console.log("[DEBUG] - Function testAndroidInterface called")
    if(!window.Android){
      alert("No se ha podido conectar correctamente con la interfaz de Android")
    } else {
      window.Android.showToast(toast);
    }
  }

  render() {
    let that = this;
    let not_loaded = <div>
        <button onClick={this.clickHandler}>Get task</button>
      </div>

    let loaded = <div>
      <ul>
        <li><p>{this.props.currentTask.title}</p></li>
      </ul>
      <button onClick={this.resetHandler}>Delete task</button>
    </div>

    const { cookies } = this.props;
    let authToken = cookies.get("authToken");
    let authUser = cookies.get("authUser");
    let cookiesstate = <p>Loading cookies...</p>;

    if(authToken && authUser){
      cookiesstate = <div>
        <ul>
          <li><p>authToken={authToken}</p></li>
          <li><p>authUser={authUser}</p></li>
        </ul>
        <button onClick={this.removeCookies}>Eliminar cookies</button>
      </div>
    } else {
      cookiesstate = <div>
      <ul>
        <p>No hay cookies cargadas</p>
      </ul>
      <button onClick={this.addCookies}>Aadir cookies</button>
    </div>
    }
    return (
      <div>
        <h1>Aplicacin de prueba para Android WebView</h1>
        <h2>Uso de cookies</h2>
        {cookiesstate}
        <br />
        <br />
        <h2>Peticiones a servicios remotos</h2>
        {this.props.currentTask.userId == 0 ? not_loaded : loaded}
        <br />
        <br />
        <h2>Interfaz para conectividad con Android</h2>
        <button onClick={(_) => {that.testAndroidInterface("El botn funciona");}}>Lanzar funcin nativa de Android</button>
      </div>
    );
  }

}


const mapStateToProps = (state, props) => {
  return ({
    currentTask: state.currentTask
  });
}

const mapActionsToProps = (dispatch, props) => {
  return bindActionCreators({
    onSetTask: setTask,
    onResetTask: resetTask
  }, dispatch); 
}

export default withCookies(connect(mapStateToProps, mapActionsToProps)(App));
